<template>
    <div
        :class="$style.bg"
        :style="{
            backgroundImage: `url(${img})`
        }"
    >
        <div
            :class="[$style.bubble, (swipeDirection !== 2) && $style.right, animate && $style.animate]"
            :style="bubbleStyle"
        >
            <div :class="$style[`arrow-${bubblePosition}`]"></div>
            <slot></slot>
        </div>
    </div>
</template>

<script>
    import { Vue, Component, Prop } from 'vue-property-decorator';
    import { Getter } from 'vuex-class';

    @Component
    export default class Background extends Vue {
        animate = false;

        @Prop({ type: String }) img;
        @Prop({ type: String, default: 'top' }) bubblePosition;
        @Prop({ type: Object }) bubbleStyle;

        @Getter('general/getDirection') swipeDirection;

        mounted() {
            setTimeout(() => {
                this.animate = true;
            }, 100);
        }
    }
</script>

<style lang="scss" module>
    @import "@/assets/scss/components/_colors.scss";

    .bg {
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        background-position: center center;
        background-size: cover;

        .bubble {
            position: absolute;
            z-index: 1;
            max-width: 420px;
            background-color: white;
            padding: 20px;
            border: 10px solid $shell-yellow;
            transform: translateX(100px);
            transition: transform 700ms ease-out;

            font-family: FuturaStd-Bold;
            font-weight: bold;
            font-size: 1.8rem;
            line-height: 2.5rem;

            &.right {
                transform: translateX(-100px);
            }

            &.animate {
                transform: translateY(0px);
            }

            p {
                margin: 0;
            }

            .arrow-top, .arrow-top-left, .arrow-bottom {
                position: absolute;
                right: 110px;
                top: -35px;
                width: 0;
                height: 0;
                border-left: 35px solid transparent;
                border-right: 0px solid transparent;
                border-bottom: 35px solid white;

                &:after {
                    content: '';
                    position: absolute;
                    z-index: -1;
                    left: -48px;
                    top: -20px;
                    width: 0;
                    height: 0;
                    border-left: 55px solid transparent;
                    border-right: 0px solid transparent;
                    border-bottom: 55px solid $shell-yellow;
                }
            }

            .arrow-bottom {
                top: auto;
                right: 70px;
                bottom: -34px;
                border-left: 35px solid transparent;
                border-right: 0 solid transparent;
                border-top: 35px solid white;
                border-bottom: none;

                &:after {
                    top: auto;
                    bottom: -22px;
                    border-left: 55px solid transparent;
                    border-right: 0px solid transparent;
                    border-top: 55px solid $shell-yellow;
                    border-bottom: none;
                }
            }

            .arrow-top-left {
                right: auto;
                left: 200px;
                border-left: 0px solid transparent;
                border-right: 35px solid transparent;

                &:after {
                    left: -8px;
                    border-left: 0px solid transparent;
                    border-right: 55px solid transparent;
                }
            }
        }
    }
</style>
